 NEAR PYH-LUOSTO NATIONAL PARK, LUOSTO")]),_c('p',{staticClass:"body-2"},[_vm._v("The glass igloos by Santas Hotel Santa Claus were opened in December 2016 at the Santa Claus Village on the Arctic Circle in Rovaniemi. The new glass-roofed, arctic view rooms offer an opportunity to experience Laplands ever-changing nature while enjoying the comforts of a hotel room. It is possible to enjoy Northern Lights, midnight sun, autumn colors and polar night. Santas Hotel Auroras glass igloos are located near the Pyh-Luosto National Park and offer a great possibility to observe the unique landscape of Luosto presenting itself outside the windows every day in a new way.")]),_c('p',{staticClass:"subheading font-weight-bold"},[_vm._v("AURORA BUBBLES, WILDERNESS HOTEL NELLIM")]),_c('p',{staticClass:"body-2"},[_vm._v("The village of Nellim by Lake Inari in Northern Lapland lies amidst the most beautiful Lappish scenery. Wilderness Hotel Nellims Aurora Bubbles are terrific little heated pods for viewing the Northern Lights. The pods have a double bed and eco-toilet for staying overnight. Try also their Aurora Camp and Northern Lights search by snowmobile.")]),_c('p',{staticClass:"subheading font-weight-bold"},[_vm._v("AURORA DOME, HARRINIVA")]),_c('p',{staticClass:"body-2"},[_vm._v("This comfortable and warm Dome is a perfect place to enjoy the magical winter scenery of lake Torassieppi lit by the northern lights. The round igloo-shaped tent with Lappish themed decoration and one transparent wall is completely insulated and a fireplace with an open fire gives out both warmth and comforting ambiance. The transparent walls of the Dome are facing north to insure the best possible view of the starlit sky and the northern lights.")])])],1)],1),_c('photo-grip',{attrs:{"items":_vm.items}})],1)],1)],1)],1)],1)}
